// General Settings
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Delius Unicase', cursive;
    background-image: url('https://source.unsplash.com/1600x900/?weather');
    background-size: cover;
}


.app-wrap {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 15px 15px;

    input {
        width: 100%;
        max-width: 280px;
        padding: 10px 15px;
        border: none;
        outline: none;
        background-color: rgba(255, 255, 255, 0.76);
        border-radius: 16px 0 16px 0;
        border-bottom: 3px solid #DF8E00;

        color: #313131;
        font-size: 20px;
        font-weight: 300;
        transition: 0.2s ease-out;

        &:focus{
            background-color: rgba(255,255,255,0.6);
        }
    }
}

main {
    flex: 1 1 100%;
    padding: 25px 25px 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .location{
        .date{
            color:#fff;
            font-size: 16px;
        }
        .city {
            color: #DF8E00;
            font-size: 32px;
            font-weight: 500;
        }
    }

    .current {
        .temp {
        color: #fff;
        font-size: 102px;
        font-weight: 900;
        margin: 30px 0px;
        text-shadow: 2px 12px rgba(0,0,0,0.7);
        
        span {font-weight: 500;}
        }
        .weather {
            color: #fff;
            font-size: 32px; 
            font-weight: 700; 
            text-shadow: 0px 4px rgba(0,0,0,0.4);
        }
        .hi-low {
            color: #fff;
            font-size: 24px;
            font-weight: 500;
        }
    }
}

